@font-face {
    font-family: "Patsy";
    src: url("./fonts/ofont.ru_Patsy\ Sans.otf");
}

html,
body,
#root {
    height: 100%;
    overflow: hidden;
    touch-action: none;
}

body {
    background-color: #f08ba4;
    background-image: url("./img/Shape\ 3.png"), url("./img/Shape\ 4.png"),
        url("./img/Shape.png"), url("./img/Shape5.png");
    background-size: cover, 80%, 70%, 70%;
    background-repeat: no-repeat, repeat, no-repeat, no-repeat;
    background-position: 0, 0, top right, bottom right;
    overflow: hidden;
    animation: backgroundAnimation 50s linear infinite;
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100%;
}

.flowers {
    width: 150px;
    margin: 0 auto;
    display: block;
    animation: flowersAnimation 5s linear infinite alternate-reverse both;
}

img {
    pointer-events: none;
}

* {
    user-select: none;
}

h1 {
    margin: 0;
    padding: 0;
}

h2 {
    margin: 0;
    padding: 0;
}

@keyframes flowersAnimation {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(1.1);
    }
}

@keyframes backgroundAnimation {
    from {
        background-position: 0, 0 0, top right, bottom right;
    }
    to {
        background-position: 0, 872px 843px, top right, bottom right;
    }
}
